<template>
  <div>
    <van-swipe :autoplay="3000" class="detail-view">
      <van-swipe-item v-for="(image, index) in item.productImg" :key="index">
        <van-image :src="image" lazy-load fit="contain" @click="showImg(index)"></van-image>
      </van-swipe-item>
    </van-swipe>
    <div class="detail-card">
      <p class="card-price">￥{{item.price}}</p>
      <p class="card-title">{{item.title}}</p>
      <p class="blank">规格参数</p>
      <p class="card-des">{{item.productDes}}</p>
    </div>
  </div>
</template>

<script>
import { ImagePreview } from "vant";

export default {
  data() {
    return {
      item: this.$route.params.item
    };
  },
  methods: {
    showParams() {
      console.log(this.$route);
    },
    showImg(index) {
      // console.log(index)
      ImagePreview({
        images: this.item.imgSrc,
        startPosition: index
      });
    }
  }
};
</script>

<style>
.detail-view {
  height: 300px;
}
p {
  margin: 10px 0;
}
.detail-card {
  /* margin-top:20rpx; */
  display: flex;
  flex-direction: column;
  font-size: 30px;
}
.card-price {
  padding-left: 10px;
  color: red;
}
.card-title {
  padding-left: 10px;
}
.blank {
  color: #fff;
  background-color: #bfbfbf;
  font-size: 20px;
  width: 100%;
  display: flex;
  align-items: center;
  padding-left: 10px;
  height: 30px;
}
</style>