            <div class="product-title">{{item.title}}</div>
            <div class="product-price">{{item.price}}</div>
          </router-link>
        </div>
      </van-collapse-item>
      <van-collapse-item title="联系我们" size="large" name="3" class="about-us">
        <div class="contact-us">
          <p>联系人：许先生</p>
          <p>联系电话：13690996886</p>
          <p>公司地址：河源市源城区丰源新村</p>
        </div>
      </van-collapse-item>
    </van-collapse>
  </div>
</template>

<script>
export default {
  data() {
    return {
      activeNames: ["2"],
      swiperList: [{ imgSrc: "/images/index-logo.png" }],
    };
  },
  computed:{
    noticeList(){
      return this.$store.state.noticeList;
    },
    productList(){
      return this.$store.state.productList;
    }
  }

};
</script>

<style>
.my-swipe .van-swipe-item {
  color: #fff;
  height: 200px;
}
.my-swipe .van-image {
  width: auto;
  height: 200px;
}
.notice-view {
  margin: 5px 0;
}
.product-view {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
  background-color: #fbfbfb;
}
.product-message {
  width: 50%;
  height: 200px;
  display: flex;
  background-color: #fff;
  flex-direction: column;
  border-radius: 10rpx;
}
.product-message .van-image {
  height: 120px;
  width: auto;
  margin: 5px;
}
.product-title {
  height: 30px;
  overflow: hidden;
  padding: 0 5px;
}
.product-price {
  height: 30px;
  color: red;
}
.product-view:after {
  content: "";
  width: 50%;
}
.about-us{
  margin-bottom: 60px;
}
.contact-us p{
  margin:5px 0;
}
</style>