<template>
  <div>
    <van-popup v-model="showPopup" round @click-overlay="showPoint">
      <van-cell-group>
        <van-field v-model="password" type="password" label="请输入密码" />
        <van-button type="primary" block @click="checkRoot">提交验证</van-button>
      </van-cell-group>
    </van-popup>
    <van-collapse v-model="activeNames">
      <!-- 公司公告功能 -->
      <van-collapse-item title="公告管理" size="large" name="1">
        <div class="blank">最新公告</div>
        <ul>
          <li
            class="notice-list"
            v-for="(item,index) in noticeList"
            :key="index"
            @click="deleteNotice(item,index)"
          >
            {{index+1}}.{{item.content}}
            <br />
            发布时间:{{item.allowTime}}
          </li>
        </ul>
        <div class="blank">新增公告</div>
        <van-cell-group>
          <van-field v-model="newNotice" type="textarea" placeholder="请输入新公告" />
          <van-button type="primary" size="small" @click="addNotice">立即发布</van-button>
        </van-cell-group>
      </van-collapse-item>
      <!-- 产品管理功能 -->
      <van-collapse-item title="发布产品" size="large" name="2">
        <van-form @submit="onSubmit">
          <van-field name="produtcType" label="产品类别">
            <template #input>
              <van-radio-group v-model="newProduct.produtcType" direction="horizontal">
                <van-radio name="handrail">楼梯扶手</van-radio>
                <van-radio name="gate">大门</van-radio>
              </van-radio-group>
            </template>
          </van-field>
          <van-field v-model="newProduct.title" required name="title" label="产品标题" />
          <van-field v-model="newProduct.price" required type="number" name="price" label="价格" />
          <van-field
            v-model="newProduct.productDes"
            type="textarea"
            autosize
            name="productDes"
            label="产品描述"
          />
          <van-uploader v-model="productImg" max-count="9" multiple />
          <div style="margin: 16px;">
            <van-button round block type="info" native-type="submit">提交</van-button>
          </div>
        </van-form>
      </van-collapse-item>
    </van-collapse>
  </div>
</template>

<script>
import { Dialog } from "vant";

export default {
  data() {
    return {
      showPopup: "",
      password: "",
      activeNames: ["0"],
      newNotice: "",
      newProduct: {
        title: "",
        price: "",
        produtcType: "handrail",
        productDes: ""
      },
      productImg: []
    };
  },
  computed: {
    noticeList() {
      return this.$store.state.noticeList;
    }
  },
  methods: {
    //设置管理员密码验证框
    showPoint() {
      Dialog.confirm({
        title: "提示",
        message: "是否离开管理页？"
      })
        .then(() => {
          // on confirm
          this.$router.go(-1);
        })
        .catch(() => {
          // on cancel
          this.showPopup = true;
        });
    },
    checkRoot() {
      if (this.password == "123456") {
        this.showPopup = false;
        this.$store.state.manageCode = "123456";
      } else {
        Dialog.alert({
          message: "密码错误！"
        });
      }
    },
    //设置发布和删除公告功能
    addNotice() {
      let noticeList = this.$store.state.noticeList;
      let allowTime = new Date().toLocaleString("chinese", { hour12: false });
      // console.log(allowTime)
      let noticeData = { content: this.newNotice, allowTime: allowTime };
      if (this.newNotice == "") {
        // alert('不能发布空白公告！')
        Dialog.alert({
          message: "不能发布空白公告！"
        }).then(() => {
          // on close
        });
      } else {
        Dialog.confirm({
          title: "提示",
          message: "是否发布“" + this.newNotice + "”这条公告?"
        })
          .then(() => {
            // on confirm
            this.addToDB(noticeData);
            noticeList.push(noticeData);
            this.newNotice = "";
          })
          .catch(() => {
            // on cancel
            return;
          });
      }
    },
    deleteNotice: function(item, index) {
      let noticeList = this.$store.state.noticeList;
      Dialog.confirm({
        title: "提示",
        message: "是否删除“" + item.content + "”这条公告?"
      })
        .then(() => {
          // on confirm
          this.delToDB(item._id);
          noticeList.splice(index, 1);
        })
        .catch(() => {
          // on cancel
          return;
        });
    },
    addToDB: function(data) {
      this.$axios({
        method: "post",
        url: "/api/addnotice.html",
        data
      })
        .then(res => {
          console.log(res);
        })
        .catch(err => {
          console.log(err);
        });
    },
    delToDB: function(id) {
      this.$axios({
        method: "get",
        url: "/api/delnotice.html",
        params: { id }
      })
        .then(res => {
          console.log(res);
        })
        .catch(err => {
          console.log(err);
        });
    },
    //设置产品上传功能
    onSubmit(values) {
      // console.log('submit', values);
      if (values.title == "" || values.price == "") {
        Dialog.alert({
          message: "产品名称和价格不能为空！"
        });
      }
      let productImg = [];

      this.productImg.forEach(item => {
        productImg.push(item.content);
      });
      values.productImg = productImg;
      console.log(values);
      Dialog.confirm({
        title: "提示",
        message: "是否发布“" + values.title + "”这件商品?"
      })
        .then(() => {
          // on confirm
          this.addProduct(values);
          this.newProduct.title = "";
          this.newProduct.price = "";
          this.newProduct.productDes = "";
          this.productImg = [];
        })
        .catch(() => {
          // on cancel
          return;
        });
    },
    addProduct(data) {
      this.$axios({
        method: "post",
        url: "/api/addproduct.html",
        data
      })
        .then(res => {
          console.log(res);
          this.freshProduct();
        })
        .catch(err => {
          console.log(err);
        });
    },
    freshProduct() {
      this.$axios
        .get("/api/getproduct.html")
        .then(res => {
          console.log(res.data);
          this.$store.state.productList = res.data;
        })
        .catch(err => {
          console.log(err);
        });
    }
  },
  created() {
    let manageCode = this.$store.state.manageCode;
    // console.log(manageCode)
    if (manageCode == "123456") {
      this.showPopup = false;
    } else {
      this.showPopup = true;
    }
  }
};
</script>

<style>
.notice-list {
  margin: 10px 0;
}
</style>