import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    noticeList: [],
    productList: [],
    manageCode:''
  },
  getters:{
    productSolt(state){
      let productList = state.productList;
      let gateList = []
      let handrailList = []
      productList.forEach((item) => {
        if(item.produtcType == 'gate'){
          gateList.push(item)
        }else{
          handrailList.push(item)
        }
      });
      return {gateList,handrailList}
    }
  },
  mutations: {
    
  },
  actions: {
  },
  modules: {
  }
})
