import Vue from 'vue'
import App from './App.vue'
import router from './router'
import Axios from 'axios'
// import Vant from 'vant';
import 'vant/lib/index.css';
// Vue.use(Vant);
import store from './store'
import {Icon,Button,Image,Lazyload,NavBar,Tabbar,TabbarItem,Swipe,SwipeItem,Collapse, CollapseItem,Sticky,Tab, Tabs,Card,Popup,Form ,Field, Cell, CellGroup,RadioGroup,Radio,Uploader} from 'vant';

Vue.use(Icon).use(Button).use(Image).use(Lazyload).use(NavBar).use(Tabbar).use(TabbarItem).use(Swipe).use(SwipeItem).use(Collapse).use(CollapseItem).use(Sticky).use(Tab).use(Tabs).use(Card).use(Popup).use(Form).use(Field).use( Cell).use(CellGroup ).use(RadioGroup).use(Radio).use(Uploader);

Vue.config.productionTip = false

Vue.prototype.$axios = Axios;//把axios存到Vue原型上

new Vue({
  router,
  store,
  render: h => h(App)
}).$mount('#app')
