<template>
  <div>
    <van-tabs v-model="active">
      <van-tab title="楼梯扶手">
        <van-card v-for="(item,index) in handrailList" :key="index" @click="getoDetail(item)" 
        :thumb="item.productImg[1]" :title="item.title" :price="item.price">
          <template #footer>
            <van-button v-show="ifManager" size="mini" @click.stop="delproduct(item)">删除</van-button>
            <van-button v-show="!ifManager" size="mini"><a href="tel:+120">联系客服</a></van-button>
          </template>
        </van-card>
      </van-tab>
      <van-tab title="大门">
        <van-card v-for="(item,index) in gateList" :key="index" @click="getoDetail(item)"
        :thumb="item.productImg[1]" :title="item.title" :price="item.price">
          <template #footer>
            <van-button v-show="ifManager" size="mini" @click.stop="delproduct(item)" >删除</van-button>
            <van-button v-show="!ifManager" size="mini"><a href="tel:+120">联系客服</a></van-button>
          </template>
        </van-card>
      </van-tab>
    </van-tabs>
    <!-- <a href="tel:+120">联系客服</a> -->
    <a href="tel://222">拨打电话</a>
  </div>
</template>

<script>
import { Dialog } from "vant";

export default {
  data() {
    return {
      active: "0"
    };
  },
  computed:{
    ifManager(){
      if(this.$store.state.manageCode == '123456'){
        return true
      }else{
        return false
      }
    },
    gateList(){
      return this.$store.getters.productSolt.gateList
    },
    handrailList(){
      return this.$store.getters.productSolt.handrailList
    }
  },
  methods:{
    getoDetail(item){
      console.log(item)
      this.$router.push({ name: 'detail', params: { id: item._id, item}})
    },
    delproduct(item){
      console.log(item)
      Dialog.confirm({
          title: "提示",
          message: "是否删除“" + item.title + "”这件商品?"
        })
          .then(() => {
            // on confirm
            this.deltoDB(item._id)
            this.deltoStore(item._id)
          })
          .catch(() => {
            // on cancel
            return;
          });
    },
    deltoDB(id){
      this.$axios({
        method:'get',
        url:'/api/delproduct.html',
        params:{id}
      })
      .then(res=>{
        console.log(res)
        
      }).catch(err=>{
        console.log(err)
      })
    },
    deltoStore(id){
      let productList = this.$store.state.productList
      productList.forEach((item,index) => {
        if(item._id == id){
          productList.splice(index,1)
        }
      });
    }
  }
};
</script>

<style>
.product-card{
  margin-bottom:2px;
  border-top: 1px solid #2b2b2b;
}
</style>