<template>
  <div id="app">
    <van-sticky>
      <van-nav-bar class="title" title="恒新实木铝业">
        <van-icon
          v-if="this.$route.name=='detail'"
          name="arrow-left"
          size="20"
          slot="left"
          @click="getBack()"
        />
      </van-nav-bar>
    </van-sticky>
    
    <div class="main-view">
      <router-view></router-view>
    </div>
    <div class="footer">
      <van-tabbar v-model="active" route>
        <van-tabbar-item icon="wap-home" to="/" exact>首页</van-tabbar-item>
        <van-tabbar-item icon="shop" to="/product">产品列表</van-tabbar-item>
        <van-tabbar-item icon="manager" to="/manage">管理页</van-tabbar-item>
      </van-tabbar>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      active: 0,
      show:false,
    };
  },
  methods: {
    getBack() {
      this.$router.go(-1); //跳转到上一次打开的页面
    },
    showPopup(){
      this.show = true
    }
  },
  mounted(){
    this.$axios.get('/api/getnotice.html')
    .then(res=>{
      console.log(res.data)
      this.$store.state.noticeList = res.data
    }).catch(err=>{
      console.log(err)
    });
    this.$axios.get('/api/getproduct.html')
    .then(res=>{
      console.log(res.data)
      this.$store.state.productList = res.data
    }).catch(err=>{
      console.log(err)
    })
  }
};
</script>

